body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2993ae;
}
* {
  font-size: 30px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.container {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 500px;
  text-align: center;
  border: 15px solid #101010;
  background: #101010;
  border-radius: 20px;
}
.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(100px, auto);
}
input[type="text"] {
  height: 100px;
  margin-bottom: 10px;
  width: 480px;
  background-color: #101010;
  border: 0px;
  color: #fff;
  text-align: right;
  font-size: 45px;
  font-weight: 500;
  letter-spacing: 1px;
}
#clear {
  grid-column: 1/3;
  grid-row: 1;
  background-color: #09323b;
  color: #fff;
}
#result {
  grid-column: 3/5;
  background-color: #09323b;
  grid-row: 5;
  color: #fff;
}
button {
  margin: 6px;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  color: #fff;
  font-weight: 500;
  background: #303030;
}
input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}
.high {
  background: #0c596d;
}
